@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap');
*{
  padding: 0;
  margin: 0;
  list-style: none;
  
}

#navbar {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-image: linear-gradient(to left bottom, rgb(114, 109, 109), rgb(0, 0, 0));

}

#categories {
  color: white;
  cursor: pointer;
  
}

nav ul li {
  display: inline-block;
  padding-left: 25px;
}
.checkbtn {
  font-size: 30px;
  color: white;
  float: left;
  line-height: 60px;
  cursor: pointer;
  display: none;
  margin-bottom: -10px;
}

.categoryLogo{
  background-color: white;
  width: 30px;
  height: 6px;
  margin: 4px;
}

#check{
  display: none;
}

.src{
  width: 20vh;
}
.searchOutput{
  background-color: white;
  position: absolute;
  top: 45px;
  right: 10px;
}
.srcAnim{
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
}


#libraryPos{
  position: absolute;
  top: 60px;
  left: 0;
  width: 99.7%;
  min-height: 100%;
  background-image: linear-gradient(to left bottom, rgb(114, 109, 109), rgb(0, 0, 0));
  border: 2px solid black;
  
}

#library{
  box-sizing: border-box;
  display:flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

}

.anime{
  background-color: rgba(121, 110, 110, 0.171);
  margin: 10px;
}

.anime:hover{
  box-shadow: 5px 5px 5px rgba(37, 33, 33, 0.863);
}

#libImg{
  width:150px;
  height: 200px;
}

.categories{
  font-size: 10px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  
}

a{
  color: rgb(250, 250, 252);
}

.title{
  margin-top: -5px;
  width: 8.4em;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


@media (max-width: 858px) {
  .checkbtn {
    display: block;
  }
    ul{
    position: fixed;
    width: 100%;
    background-image: linear-gradient(to left bottom, rgb(114, 109, 109), rgb(0, 0, 0));
    top: 60px;
    left: -100%;
    text-align: center;
    transition: all .5s;
    z-index: 1;
  }
  nav ul li{
    display: block;
    margin: 50px 0px;
    line-height: 30px;
  }
  nav ul li a {
    font-size: 20px;
  }
  #check:checked ~ ul {
    left: 0;
  }

  #libraryPos{
    width: 99%;
    left: 0;
    top: 60px;
  }
}